html,body{
  padding:0;
  margin:0;
}
.app_container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 98vh;
  width: 99vw;
}
.screen,
.keyboard {
  width: 70%;
}
.screen {
  text-align: right;
  font-size: 1.5rem;
  border: none;
  background: #e5e5e5;
  padding: 0.5rem;
}
.screen_number {
  text-align: right;
  font-size: 1.5rem;
  border: none;
  margin: 0;
  padding: 1rem;
}
.left_side {
  display: flex;
  flex: 0 0 100%;
  order: 1;
  background: moccasin;
  width: 30%;
  justify-content: center;
  text-align: right;
  overflow: auto;
  direction: rtl;
  justify-content: center;
 
}
.letters_container{
  padding-right:1rem
}
.keyboard {
  display: flex;
  flex-wrap: wrap;
  padding: auto;
  background-color: white;
  justify-content: center;
  align-content: center;
}
.keyboard_key {
  width: 80px;
  height: 80px;
  margin: 0.2rem;
  padding: 0.5rem, 0.5rem;
  vertical-align: middle;
  font-size: 1.5rem;
  border-radius: 5px;
  border: none;
  border-color: #fff;
}
.keyboard_key:hover {
  position: relative;
  top: 1px;
  left: 1px;
  border-color: #e5e5e5;
  cursor: pointer;
}
.letter_container {
  display: inline-block;
  text-align: center;
 
  font-size: 1rem;
}
.letter_input {
  display: inline-block;
  width: 30px;
  vertical-align: middle;
}
.keyboard_row {
  display: flex;
  justify-content: center;
  width: 80%;
}
.key_space {
  display: block;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .app_container {
    display: flex;

    flex-direction: row;
    height: 100vh;
  }
  .screen,
  .keyboard {
    width: 100%;
  }
  .keyboard {
    display: none;
  }
  .screen_number {
    font-size: 1rem;
    padding: 0.5rem;
    text-align: right;
    width: 100%;
  }
  .keyboard_key {
    display: inline-block;
    width: 40px;
    height: 40px;
    padding: 0.5rem, 0.5rem;
    vertical-align: middle;
  }
  .key_space {
    display: block;
    width: 100px;
  }
  
}
